<template>
  <div class="add-relation-box">
    <gs-button
      :disabled="!categoryGuid || disabled"
      type="primary"
      @click="showModal"
    >添加关联</gs-button>
    <gs-modal
      v-model="visible"
      :width="800"
      :height="360"
      top="60px"
      class="add-relation-modal"
      title="添加关联表"
    >
      <gs-row v-loading="loading">
        <gs-col
          v-bind="leftSpan"
          class="relation-col"
        >
          <label class="relation-label">待添加</label>
          <gs-search
            :value="keyword"
            :placeholder="searchType.label === 0 ? '请输入数据库' : '请输入数据表'"
            @search="handleSearch"
          >
            <template slot="prepend">
              <gs-select v-model="searchType.label">
                <gs-option
                  v-for="item in searchTypes"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </gs-select>
            </template>
          </gs-search>
          <div
            class="database-box"
            @scroll="handleScroll"
          >
            <gs-infinite-tree
              v-if="!isEmpty && !Object.keys(tables).length"
              ref="tree"
              :data="treeData"
              :render-content="renderTreeNode"
              :load="lazyLoad"
              :height="266"
              node-key="value"
              show-checkbox
              lazy
              @current-change="handleSelectChange"
              @scroll="handleScroll"
              @node-click="nodeClick"
            />
            <gs-checkbox-group
              v-if="Object.keys(tables).length"
              v-model="selectedNodes"
              class="checkbox-group"
              @change="chooseTables()"
            >
              <gs-checkbox
                v-for="item in tables.lists || []"
                :label="item.tableId"
                :key="item.tableId"
                :disabled="item.check === 1"
              >{{ item.tableName }}</gs-checkbox>
            </gs-checkbox-group>
            <div
              v-show="isEmpty && !loading"
              class="database-empty"
            >
              暂无数据
            </div>
          </div>
        </gs-col>
        <gs-col
          v-bind="rightSpan"
          class="relation-col"
        >
          <label class="relation-label">已选表 ({{ selectedTables.length }})</label>
          <div class="table-header">
            <span class="name">数据表</span>
            <span class="parent">所属数据库</span>
            <span class="path">路径</span>
          </div>
          <div
            class="table"
            style="height: 286px"
          >
            <RecycleScroller
              :items="selectedTables"
              :item-size="36"
              key-field="tableId"
              style="height: 100%"
            >
              <template v-slot="{item, index}">
                <div>
                  <span
                    :title="item.tableName"
                    class="name"
                  >{{ item.tableName }}</span>
                  <span
                    :title="item.databaseName"
                    class="parent"
                  >{{ item.databaseName }}</span>
                  <span
                    :title="item.path"
                    class="path"
                  >{{ item.path }}</span>
                  <span class="operation-btn">
                    <gs-icon
                      name="close"
                      mode="button"
                      @click="removeTable(item)"
                    />
                  </span>
                </div>
              </template>
            </RecycleScroller>
          </div>
        </gs-col>
      </gs-row>
      <template slot="footer">
        <gs-button
          :loading="isCreating"
          type="primary"
          @click="handleConfirm"
        >确定</gs-button>
        <gs-button
          :disabled="isCreating"
          @click="close"
        >取消</gs-button>
      </template>
    </gs-modal>
  </div>
</template>

<script>
import * as metadataService from 'src/service/metadata-management/metadata-management-list';
import * as relationService from 'src/service/relation';
import { RecycleScroller } from 'vue-virtual-scroller';
export default {
  components: {
    RecycleScroller
  },
  props: {
    categoryGuid: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      visible: false,
      leftSpan: {
        span: 8,
        xs: {
          span: 24
        }
      },
      rightSpan: {
        span: 15,
        offset: 1,
        xs: {
          span: 24,
          offset: 0
        }
      },
      keyword: '',
      searchType: {
        current: 0,
        label: 0
      },
      searchTypes: [
        {
          value: 0,
          label: '库'
        },
        {
          value: 1,
          label: '表'
        }
      ],
      page: 1,
      pageSize: 10,
      selectedNodes: [],
      treeData: [],
      loading: false,
      metadatas: {
        sum: 0,
        lists: []
      },
      tables: {},
      currentAllRelations: {
        lists: []
      },
      tableChecked: [],
      tableMap: {},
      defaultCheckedKeys: [],
      indeterminateKeys: [],
      dbMap: {}
    };
  },

  computed: {
    isEmpty() {
      return this.searchType.current === 0
        ? this.treeData.length === 0
        : (this.tables.lists ? this.tables.lists.length === 0 : true);
    },
    selectedTables() {
      return this.selectedNodes.map(item => this.tableMap[item]);
    },
    isCreating() {
      return false;
    }
  },

  watch: {
    visible(val) {
      this.keyword = '';
      this.searchType = {
        current: 0,
        label: 0
      };
      this.selectedNodes = [];
      if (val) {
        this.page = 1;
        this.metadatas = {
          sum: 0,
          lists: []
        };
        this.tables = {};
        this.treeData = [];
        this.tableChecked = [];
        this.defaultCheckedKeys = [];
        this.dbMap = {};
        this.queryMetadatas();
      }
    }
  },
  create() {
  },
  methods: {
    showModal() {
      this.visible = true;
    },

    renderTreeNode(h, {node, data}) {
      let itemClass = 'table-tree-node-label';
      const isParent = !!data.children;
      const isDeleted = data.status === 'DELETED';
      const label = data.label;

      itemClass += isDeleted ? ' is-deleted' : '';
      itemClass += isParent ? ' is-parent' : '';
      return (
        <div class="table-tree-node-content">
          <span
            title={label}
            class={itemClass}
          >
            {label}
          </span>
          <span
            class="status"
          >
            {isDeleted ? ' (已删除)' : ''}
          </span>
        </div>
      );
    },

    async queryMetadatas(isSearch = false) {
      const {
        categoryGuid,
        keyword,
        page,
        pageSize,
        tables,
        metadatas
      } = this;
      const total = this.searchType.current === 0 ? metadatas.sum : tables.sum;
      const offset = (page - 1) * pageSize;

      if (offset >= total && total > 0) {
        return;
      }
      const params = {
        categoryGuid,
        param: {
          query: keyword,
          offset,
          limit: pageSize
        }
      };

      this.loading = true;
      if (this.searchType.current === 0) {
        const data = await metadataService.getAllMetadataList(params);
        this.loading = false;
        this.metadatas = {
          lists: [...this.metadatas.lists, ...data.lists],
          sum: data.sum
        };
        const append = data.lists.map(item => {
          return {
            label: item.dbName,
            value: item.databaseGuid,
            status: item.databasestatus
          };
        });
        if (isSearch) this.treeData = [];
        this.treeData = [...this.treeData, ...append];
        this.treeData = this.treeData.map(i => {
          if (this.dbMap[i.value]) {
            i.children = this.dbMap[i.value];
          }
          return i;
        });
        this.defaultCheckedKeys = this.metadatas.lists
          .filter(i => i.check === 1)
          .map(i => i.databaseGuid);
        this.preloadIndeterminateTable(data);
        this.$nextTick(_ => {
          const isChecked = this.$refs.tree.getCheckedKeys();
          this.$refs.tree.setCheckedKeys([...isChecked, ...this.defaultCheckedKeys, ...this.selectedNodes]);
          this.defaultCheckedKeys.map(keys => {
            const index = this.treeData.findIndex(i => i.value === keys);
            this.treeData[index].disabled = true;
          });
          this.treeData.map(i => {
            i.status === 'DELETED' && (i.disabled = true);
            return i;
          });
        });
      } else {
        const data = await metadataService.getTableList(params);
        this.tables = {
          lists: this.tables.lists ? [...this.tables.lists, ...data.lists] : data.lists,
          sum: data.sum
        };
        data.lists.map(i => {
          this.$set(this.tableMap, i.tableId, i);
        });
      }
      this.loading = false;
    },

    handleSearch(value) {
      this.searchType.current = this.searchType.label;
      if (this.searchType.current === 0) {
        this.tables = {};
      }
      if (this.keyword !== value) {
        if (this.searchType.current === 0) {
          this.treeData = [];
          this.metadatas = {
            lists: [],
            sum: 0
          };
        } else {
          this.tables = {};
        }
      }
      this.keyword = value;
      this.page = 1;
      this.queryMetadatas(true);
    },

    handleScroll(e) {
      const distance = 20;
      const wrapper = e.target;
      const height = wrapper.offsetHeight;
      const top = wrapper.scrollTop;
      const scrollHeight = wrapper.scrollHeight;

      if (scrollHeight - height - top <= distance && !this.loading) {
        this.page += 1;
        this.queryMetadatas();
      }
    },

    findIndex(value) {
      return this.selectedNodes.findIndex(i => i === value);
    },

    handleSelectChange(node, checked, indeterminate) {
      if (node.$node.level === 1) {
        if (checked) {
          if (this.defaultCheckedKeys.includes(node.data.value)) {
            return;
          }
          if (indeterminate) {
            // 1. 点击库由全选变为半选，2. 点击表导致库从全选变为半选 都会触发库的change事件。
            const checkedChildLength = node.childNodes.filter(i => i.$node.checked).length;
            const disabledCheckedChildrenLength = node.data.children.filter(i => i.disabled).length;
            if (checkedChildLength === disabledCheckedChildrenLength) {
              node.data.children.map(i => {
                let index = this.findIndex(i.value);
                if (index !== -1 && !i.disabled) this.selectedNodes.splice(index, 1);
              });
            }
          } else {
            // 直接勾选，如果有子数据，则push，否则从接口获取数据
            if (node.data.children) {
              node.data.children.map(i => {
                !i.disabled && this.selectedNodes.push(i.value);
              });
              this.selectedNodes = Array.from(new Set(this.selectedNodes));
            } else {
              this.loading = true;
              this.getTableFromDB(node.data.value).then(data => {
                const table = data.lists.map(i => {
                  this.$set(this.tableMap, i.tableId, i);
                  if (!i.check) this.selectedNodes.push(i.tableId);
                  return {
                    label: i.tableName,
                    value: i.tableId,
                    leaf: true,
                    disabled: i.status === 'DELETED' ? true : !!i.check,
                    status: i.status
                  };
                });
                this.dbMap[node.data.value] = table;
                const nodeIndex = this.treeData.findIndex(i => i.value === node.data.value);
                this.$set(this.treeData[nodeIndex], 'children', table);
                this.$nextTick(_ => {
                  const isChecked = this.$refs.tree.getCheckedKeys();
                  this.$refs.tree.setCheckedKeys([...isChecked, ...this.defaultCheckedKeys, node.data.value]);
                });
                this.loading = false;
                return data;
              });
            }
          }
        } else {
          if (node.data.children) {
            node.data.children.map(i => {
              this.spliceSelectNodes(i.value);
            });
          }
        }
      } else if (node.$node.level === 2) {
        if (node.data.disabled) return;
        if (checked) {
          if (!this.selectedNodes.includes(node.data.value)) this.selectedNodes.push(node.data.value);
        } else {
          this.spliceSelectNodes(node.data.value);
        }
      }
    },

    removeTable(row) {
      if (this.selectedNodes.length > 500) {
        this.loading = true;
        setTimeout(_ => {
          this.loading = false;
        }, 1000);
      }
      if (this.$refs.tree && this.$refs.tree.getCheckedKeys().includes(row.tableId)) {
        this.$refs.tree.setChecked(row.tableId, false);
        this.spliceSelectNodes(row.tableId);
      } else {
        this.spliceSelectNodes(row.tableId);
      }
    },

    handleConfirm() {
      const guids = this.selectedTables.map(item => {
        return {
          tableGuid: item.tableId,
          tableName: item.tableName,
          dbName: item.databaseName,
          status: item.status,
          createTime: item.createTime
        };
      });

      if (guids.length === 0) {
        this.$Message.warning('请选择要关联的表');
        return;
      }
      if (this.categoryGuid) {
        relationService.create({
          params: guids,
          categoryGuid: this.categoryGuid
        }).then(() => {
          this.$Message.success('添加关联成功');
          this.$emit('create-success');
          this.close();
        });
      } else {
        this.$Message.warning('请选择要关联的目录');
      }
    },

    close() {
      this.visible = false;
    },

    lazyLoad(node, resolve) {
      this.getTableFromDB(node.data.value).then(data => {
        const table = data.lists.map(i => {
          this.$set(this.tableMap, i.tableId, i);
          return {
            label: i.tableName,
            value: i.tableId,
            leaf: true,
            disabled: i.status === 'DELETED',
            status: i.status
          };
        });
        this.dbMap[node.data.value] = table;
        resolve(table);
        return data;
      }).then(data => {
        this.setTableCheck(data, node.data.value);
      });
    },
    nodeClick(node, e) {
      // 设置默认勾选时，会出发current-change事件，原因是组件中使用watch监听check的状态来触发
    },
    chooseTables(val) {
    },
    getTableFromDB(id) {
      return metadataService.getTableFromDB({
        databaseGuid: id,
        offset: 0,
        limit: -1,
        categoryGuid: this.categoryGuid
      });
    },
    setTableCheck(data, id) {
      this.$nextTick(_ => {
        const disabledCheck = data.lists
          .filter(i => i.check === 1)
          .map(i => i.tableId);
        const checked = this.$refs.tree.getCheckedKeys();
        this.$refs.tree.setCheckedKeys([...checked, ...this.defaultCheckedKeys, ...disabledCheck]);
        const index = this.treeData.findIndex(i => i.value === id);
        this.treeData[index].children.map(i => {
          if (disabledCheck.includes(i.value) || i.status === 'DELETED') {
            i.disabled = true;
          } else {
            i.disabled = false;
          }
        });
      });
    },
    spliceSelectNodes(value) {
      let index = this.findIndex(value);
      if (index !== -1) this.selectedNodes.splice(index, 1);
    },
    preloadIndeterminateTable(data) {
      // 预加载半选的库
      data.lists
        .filter(i => i.check === 2)
        .map(id => {
          metadataService.getTableFromDB({
            databaseGuid: id.databaseGuid,
            offset: 0,
            limit: -1,
            categoryGuid: this.categoryGuid
          }).then(data => {
            const table = data.lists.map(i => {
              this.$set(this.tableMap, i.tableId, i);
              return {
                label: i.tableName,
                value: i.tableId,
                leaf: true,
                status: i.status,
                disabled: i.status === 'DELETED'
              };
            });
            const nodeIndex = this.treeData.findIndex(i => i.value === id.databaseGuid);
            this.$set(this.treeData[nodeIndex], 'children', table);
            this.dbMap[id.databaseGuid] = table;
            return data;
          }).then(data => {
            this.setTableCheck(data, id.databaseGuid);
          });
        });
    }
  }
};
</script>




