<template>
  <div class="add-owner">
    <div class="choose-table">
      <p class="title">已选数据表</p>
      <div class="content">
        <el-table
          :data="table || []"
          max-height="220"
        >
          <el-table-column
            type="index"
            label="序号"
          />
          <el-table-column
            prop="tableName"
            label="数据表名"
          />
          <el-table-column
            prop="path"
            label="路径"
          />
        </el-table>
      </div>
    </div>
    <div class="choose-owner">
      <p class="title">选择数据Owner</p>
      <div class="content">
        <div class="chosen">
          <p>已选择(已选择{{ chosenOrgaItem.length || 0 }}项)</p>
          <ul class="chosen-list">
            <li class="title">
              <span
                class="name"
              >名称</span>
              <span
                class="path"
              >所属组织架构</span>
            </li>
            <li
              v-for="(item, index) in chosenOrgaItem"
              :key="item.id"
            >
              <span
                :title="item.name"
                class="name"
              >{{ item.name }}</span>
              <span
                :title="item.path"
                class="path"
              >{{ item.path }}</span>
              <gs-button
                type="text-primary"
                icon="delete"
                @click="deleteOrga(item, index)"
              />
            </li>
          </ul>
        </div>
        <gs-search
          v-model="filterKey"
          placeholder="请输入组织名称"
          @search="searchOrga"
          @clear="closeTable"
        />
        <div class="breadcrumb">
          <gs-button
            type="text-primary"
            @click="refreshOrga"
          >
            <i
              :class="{'animation-rotate': refreshLoading}"
              class="iconfont icon-shuaxin2"
            />
          </gs-button>
          <el-breadcrumb
            v-show="!showSearchTable"
            separator="/"
          >
            <el-breadcrumb-item
              v-for="(item, index) in breadcrumb"
              :key="index"
            >
              <span
                class="name"
                @click="breadcrumbClick(item, index)"
              >{{ item.name }}</span>
            </el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div
          v-loading="loading"
          class="organization"
        >
          <ul
            v-show="!showSearchTable"
            class="organization-list"
          >
            <template v-if="activeLists && activeLists.length">
              <gs-checkbox-group v-model="chosenOrga">
                <li
                  v-for="item in activeLists || []"
                  :key="item.id"
                  class="name"
                >
                  <gs-checkbox
                    :label="item.id"
                    v-model="chosenOrga"
                  >{{ '' }}</gs-checkbox>
                  <span
                    class="label"
                    title="点击进入下一层级"
                    @click="getOrgaInfo(item)"
                  >
                    <i class="iconfont icon-zuzhi" />{{ item.name }}
                  </span>
                </li>
              </gs-checkbox-group>
            </template>
            <li v-else>暂无数据</li>
            <li
              v-show="showLoadMoreBtn"
              class="load-more-btn"
            >
              <gs-button
                type="text-primary"
                @click="getOrgaInfo(activeItem)"
              >点击加载更多</gs-button>
            </li>
          </ul>
          <el-table
            v-show="showSearchTable"
            ref="searchTable"
            :data="activeSearchLists"
            @selection-change="handleSelectionChange"
          >
            <el-table-column
              type="selection"
              width="55"
            />
            <el-table-column
              prop="name"
              label="名称"
            />
            <el-table-column
              prop="path"
              label="所属组织架构"
            />
          </el-table>
          <div class="load-more-btn">
            <gs-button
              v-show="showSearchLoadMoreBtn"
              type="text-primary"
              @click="searchOrga(filterKey)"
            >点击加载更多</gs-button>
          </div>
        </div>
      </div>
    </div>
    <p class="mark">注：数据表Owner作为数据管理员，负责审批使用该表数据的申请。</p>
    <div class="operation">
      <gs-button
        @click="cancel"
      >取消</gs-button>
      <gs-button
        type="primary"
        @click="confirm"
      >确定</gs-button>
    </div>
  </div>
</template>
<script>
import {
  mapActions,
  mapState
} from 'vuex';
import dataTable from 'src/components/data-table';
const GET_ORGA_LIMIT = 10;
export default {
  components: {
    dataTable
  },
  props: {
    table: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      chosenOrga: [],
      filterKey: '',
      breadcrumb: [
        {
          name: '组织架构',
          id: window.systemConfigs['FIRST_LEVEL_ORGA']
        }
      ],
      storeOrgaData: [],
      searchOffset: 0,
      showSearchTable: false,
      loading: false,
      activeItem: {
        name: '组织架构',
        id: window.systemConfigs['FIRST_LEVEL_ORGA']
      },
      offset: 0,
      activeLists: [],
      showLoadMoreBtn: false,
      activeSearchLists: [],
      showSearchLoadMoreBtn: false,
      noCheckedIds: [], // 搜索时过滤出列表中没有的id,
      refreshLoading: false
    };
  },
  computed: {
    ...mapState('relation', {
      organizations: 'organizations',
      searchOrganizations: 'searchOrganizations'
    }),
    allOrgaInfo() {
      const result = {};
      function flat(nodes) {
        if (!nodes || nodes.length === 0) return [];
        nodes.forEach(node => {
          result[node.id] = node;
          return flat(node.children);
        });
      }
      const arr = [...this.storeOrgaData, ...(this.searchOrganizations.lists || [])];
      flat(arr);
      return result;
    },
    chosenOrgaItem() {
      return this.chosenOrga.map(item => {
        return this.allOrgaInfo[item];
      });
    }
  },
  mounted() {
    this.loading = true;
    this.getOrganization({
      pid: this.breadcrumb[0].id,
      param: {
        limit: GET_ORGA_LIMIT,
        offset: 0
      }
    }).then(_ => {
      this.loading = false;
      this.storeOrgaData = JSON.parse(JSON.stringify(this.organizations.lists));
      this.activeLists = this.organizations.lists;
      this.showLoadMoreBtn = this.activeLists.length < this.organizations.sum;
    });
  },
  methods: {
    ...mapActions('relation', [
      'getOrganization',
      'queryOrganizations',
      'setOwners',
      'getLastestOrga'
    ]),
    searchOrga(val) {
      this.loading = true;
      this.searchOffset = this.storeSearchValue === val ? this.searchOffset + 10 : 0;
      this.queryOrganizations({
        query: val,
        offset: this.searchOffset,
        limit: GET_ORGA_LIMIT
      }).then(_ => {
        this.loading = false;
        if (this.storeSearchValue === val) {
          this.activeSearchLists = this.activeSearchLists.concat(this.searchOrganizations.lists);
        } else {
          this.activeSearchLists = this.searchOrganizations.lists;
        }
        this.storeSearchValue = val;
        this.showSearchLoadMoreBtn = this.activeSearchLists.length < this.searchOrganizations.sum;
        const checkedItem = (this.activeSearchLists || []).filter(i => this.chosenOrga.includes(i.id));
        this.$nextTick(_ => {
          checkedItem.forEach(row => {
            this.$refs.searchTable.toggleRowSelection(row, true);
          });
        });
        const activeSearchIds = this.activeSearchLists.map(i => i.id);
        this.noCheckedIds = this.chosenOrga.filter(i => !activeSearchIds.includes(i));
      });
      this.showSearchTable = true;
    },
    getOrgaInfo(item, isBread = false, refresh = false) {
      if (item) {
        if (this.activeItem.id === item.id && !refresh && !isBread) {
          this.offset += 10;
        } else {
          this.offset = 0;
          !isBread && this.breadcrumb.push(item);
        }
        this.loading = true;
        this.getOrganization({
          pid: item.id,
          param: {
            limit: GET_ORGA_LIMIT,
            offset: this.offset
          }
        }).then(_ => {
          this.loading = false;
          this.activeLists = this.activeItem.id === item.id && !refresh && !isBread
            ? [...this.activeLists, ...this.organizations.lists]
            : this.organizations.lists;
          this.showLoadMoreBtn = this.activeLists.length < this.organizations.sum;
          this.activeItem = item;
          if (item.id === window.systemConfigs['FIRST_LEVEL_ORGA'] && !isBread) {
            const ids = this.storeOrgaData.map(i => i.id);
            this.organizations.lists.map(i => {
              if (!ids.includes(i.id)) {
                this.storeOrgaData.push(i);
              }
            });
          }
          !isBread && this.setStoreOrgaData(item, this.organizations.lists);
        });
      }
    },
    breadcrumbClick(item, index) {
      this.breadcrumb = this.breadcrumb.slice(0, index + 1);
      this.getOrgaInfo(item, true);
    },
    deleteOrga(item, index) {
      this.chosenOrga.splice(index, 1);
      const checkedItem = (this.activeSearchLists || []).filter(i => this.chosenOrga.includes(i.id));
      this.$refs.searchTable.clearSelection();
      checkedItem.forEach(row => {
        this.$refs.searchTable.toggleRowSelection(row);
      });
    },
    setStoreOrgaData(item, setData = []) {
      function transerDF(rootItem, id) {
        var value = null;
        (function recurision(currentItem) {
          for (let i = 0; i < currentItem.length; i++) {
            if (currentItem[i].id === id) {
              value = currentItem[i];
              break;
            };
            if (currentItem[i] && currentItem[i].children) {
              recurision(currentItem[i].children);
            };
          }
        })(rootItem);
        return value;
      }
      const i = transerDF(this.storeOrgaData, item.id);
      if (i) {
        if (!i.children) {
          this.$set(i, 'children', setData);
        } else {
          const ids = i.children.map(i => i.id);
          const data = setData.filter(i => !ids.includes(i.id));
          i.children.push(...data);
        }
      }
    },
    handleSelectionChange(val) {
      if (this.showSearchTable) {
        const ids = val.map(i => i.id);
        this.chosenOrga = Array.from(new Set([
          ...ids,
          ...this.noCheckedIds
        ]));
      }
    },
    confirm() {
      if (!this.chosenOrga.length) {
        this.$Message.warning('请选择组织架构');
        return false;
      }
      const ids = this.table.map(item => { return item.tableGuid; });
      const arr = this.chosenOrgaItem.map(item => {
        return {
          id: item.id,
          type: item.type,
          pkid: item.pkid
        };
      });
      this.setOwners({tables: ids, owners: arr}).then(res => {
        this.$Message.success('设置Owner成功');
        this.$emit('cancel');
        this.$emit('refreshtb');
      });
    },
    cancel() {
      this.$emit('cancel');
    },
    closeTable() {
      this.showSearchTable = false;
      this.searchOffset = 0;
      this.storeSearchValue = null;
      this.showSearchLoadMoreBtn = false;
      this.noCheckedIds = [];
    },
    refreshOrga() {
      this.refreshLoading = true;
      this.getLastestOrga().then(_ => {
        this.refreshLoading = false;
        this.$Message.success('刷新组织架构成功');
        this.offset = 0;
        this.getOrgaInfo(this.activeItem, true, true);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../../../components/style/var";
.add-owner {
  min-height: 70vh;
  .breadcrumb {
    display: flex;
    padding: 8px 0;
  }
  .choose-table {
    margin-bottom: 10px;
  }
  .title  {
    margin-bottom: 10px;
  }
  .content {
    padding: 16px;
    box-shadow: 0 0 4px 0 rgba(0,0,0,0.07);
  }
  .chosen {
    border-bottom: 1px solid #EFF0F1;
    padding: 10px;
  }
  .chosen-list {

    height: 140px;
    overflow: auto;
  }
  .organization-list {
    height: 180px;
    overflow: auto;
  }
  .chosen-list {
    .title {
      border-bottom: 1px solid #EFF0F1;
    }
    .name {
      display: inline-block;
      width: 200px;
    }
    .path {
      display: inline-block;
      width: 100%;
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }
    .gs-button-icon {
      display: none;
      float: right;
    }
    li {
      margin: 4px 0;
      padding: 4px;
      display: flex;
      &:hover {
      background: #f5f7fa;
        color: $primary-color;
        .gs-button-icon {
          display: block;
        }
      }
    }
  }
  .organization {
    position: relative;
  }
  .organization-list {
    .name {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-bottom: 6px;
      width: 100%;
      color: $primary-color;
      &:hover {
        cursor: pointer;
        background: #ecf5ff;
      }
    }
    .gs-checkbox {
      margin-right: 16px;
      & + .gs-checkbox {
        margin-left: 0;
      }
    }
    .label {
      width: 100%;
      font-size: 14px;
    }
    .gs-checkbox-group {
      width: 100%;
    }
  }
  .gs-search {
    margin: 10px 0;
  }
  .el-breadcrumb {
    min-height: 14px;
    border-bottom: 1px solid #EFF0F1;
    .name {
      &:hover {
          color: $primary-color;
          cursor: pointer;
        }
    }
  }
  .operation {
    margin-top: 16px;
    display: flex;
    justify-content: flex-end;
  }
  .mark {
    color: #D0021B;
  }
  .load-more-btn {
    display: flex;
    justify-content: center;
    padding-top: 10px;
  }
}
</style>



